'use client';
import { useEffect, useState } from 'react';
import { DataService } from '@/utils/api';
import PestTrendChart from '@/components/Dashboard/PestTrendChart';
import ImageUpload from '@/components/Dashboard/ImageUpload';

interface SensorData {
    temperature: number;
    humidity: number;
    soilMoisture: number;
    riskLevel: string;
}

interface PestData {
    pestType: string;
    count: number;
    confidence: number;
    createdAt: string;
}

export default function Home() {
    const [pestData, setPestData] = useState<PestData[]>([]);
    const [sensorData, setSensorData] = useState<SensorData[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const [pests, sensors] = await Promise.all([
                    DataService.getPestDetections(),
                    DataService.getSensorData()
                ]);
                setPestData(pests);
                setSensorData(sensors);
            } catch (err) {
                setError('Backend connection offline.');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, []);

    if (loading) return (
        <div className="flex min-h-screen items-center justify-center bg-[#0f172a]">
            <div className="w-10 h-10 border-4 border-emerald-500/20 border-t-emerald-500 rounded-full animate-spin"></div>
        </div>
    );

    const latest = sensorData[sensorData.length - 1] || { temperature: 0, humidity: 0, soilMoisture: 0, riskLevel: 'Stable' };

    return (
        <div className="min-h-screen bg-[#020617] text-slate-200 selection:bg-emerald-500/30">
            {/* Dark Header */}
            <nav className="border-b border-slate-800 bg-[#020617]/80 backdrop-blur-md sticky top-0 z-50">
                <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
                    <div className="flex items-center gap-4">
                        <div className="p-2 bg-emerald-500/10 border border-emerald-500/20 rounded-xl">
                            <div className="w-6 h-6 bg-emerald-500 rounded-lg shadow-[0_0_15px_rgba(16,185,129,0.5)]"></div>
                        </div>
                        <h1 className="text-xl font-black tracking-tighter text-white">EXPEL<span className="text-emerald-500">AI</span></h1>
                    </div>
                    <div className="flex items-center gap-2 px-4 py-1.5 bg-slate-900 border border-slate-800 rounded-full">
                        <div className="w-2 h-2 bg-emerald-500 rounded-full shadow-[0_0_8px_rgba(16,185,129,0.8)] animate-pulse"></div>
                        <span className="text-[10px] font-bold text-slate-400 uppercase tracking-widest">Live Terminal</span>
                    </div>
                </div>
            </nav>

            <main className="max-w-7xl mx-auto p-8">
                {/* Statistics Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-10">
                    <StatCard label="Air Temp" value={`${latest.temperature}Â°C`} color="emerald" />
                    <StatCard label="Humidity" value={`${latest.humidity}%`} color="blue" />
                    <StatCard label="Moisture" value={`${latest.soilMoisture || 48}%`} color="cyan" />
                    <StatCard label="AI Risk" value={latest.riskLevel} color={latest.riskLevel === 'High' ? 'red' : 'emerald'} />
                </div>

                {/* AI Detection Upload */}
                <ImageUpload />

                {/* Chart Visualization */}
                <div className="bg-slate-900/50 border border-slate-800 p-8 rounded-[2rem] shadow-2xl">
                    <div className="flex justify-between items-center mb-10">
                        <div>
                            <h2 className="text-lg font-bold text-white">Detection Matrix</h2>
                            <p className="text-sm text-slate-500 font-medium">Historical pest population metrics</p>
                        </div>
                        <div className="flex gap-2">
                            <div className="h-8 w-8 rounded-lg bg-slate-800 flex items-center justify-center cursor-pointer hover:bg-slate-700 transition-colors">
                                <span className="text-slate-400 text-xs">ðŸ“Š</span>
                            </div>
                        </div>
                    </div>
                    <div className="h-[450px]">
                        <PestTrendChart data={pestData} />
                    </div>
                </div>
                {/* Recent History Section */}
                <div className="mt-10 bg-slate-900/50 border border-slate-800 p-8 rounded-[2rem] shadow-2xl">
                    <h2 className="text-lg font-bold text-white mb-6">Recent Activity Log</h2>
                    <div className="space-y-4 max-h-[400px] overflow-y-auto pr-2 custom-scrollbar">
                        {pestData.map((report: any, index: number) => (
                            <div key={index} className="p-4 bg-slate-800/40 border border-slate-700/50 rounded-2xl flex justify-between items-center hover:bg-slate-800/60 transition-colors">
                                <div>
                                    <div className="flex items-center gap-2 mb-1">
                                        <span className={`w-2 h-2 rounded-full ${report.severity === 'High' ? 'bg-red-500' : 'bg-emerald-500'}`}></span>
                                        <p className="text-white font-bold">{report.pestType}</p>
                                    </div>
                                    <p className="text-xs text-slate-500">{new Date(report.createdAt).toLocaleString()}</p>
                                </div>
                                <div className="text-right">
                                    <p className="text-emerald-400 font-black text-lg">{report.count}</p>
                                    <p className="text-[10px] text-slate-500 uppercase tracking-widest">Pests Found</p>
                                </div>
                            </div>
                        ))}
                        {pestData.length === 0 && (
                            <p className="text-center text-slate-500 py-10">No history found yet. Start by uploading an image!</p>
                        )}
                    </div>
                </div>
            </main>
        </div>
    );
}

function StatCard({ label, value, color }: { label: string, value: string, color: 'emerald' | 'blue' | 'cyan' | 'red' }) {
    const glows: any = {
        emerald: "shadow-emerald-500/10 border-emerald-500/20",
        blue: "shadow-blue-500/10 border-blue-500/20",
        cyan: "shadow-cyan-500/10 border-cyan-500/20",
        red: "shadow-red-500/10 border-red-500/20"
    };

    const textColors: any = {
        emerald: "text-emerald-400",
        blue: "text-blue-400",
        cyan: "text-cyan-400",
        red: "text-red-400"
    };

    return (
        <div className={`bg-slate-900/40 border p-6 rounded-2xl shadow-xl backdrop-blur-sm transition-all hover:-translate-y-1 ${glows[color]}`}>
            <p className="text-[10px] font-black text-slate-500 uppercase tracking-widest mb-4">{label}</p>
            <p className={`text-3xl font-black tracking-tight ${textColors[color]}`}>{value}</p>
        </div>
    );
}